<template>
	<renderContainerLay
		v-if="isRender" 
		:options="PageConf.content"
		:outParams="outParams"
		@method1="_method1"
		@method2="_method2"
		@method3="_method3"
		@method4="_method4"
	>
	</renderContainerLay>	
</template>

<script type="text/babel">
import fetch from '@/api/fetch';
import util from '@/common/js/util';
import renderContainerLay from '@/components/layout/renderContainerLay'
export default {
	data(){
		return {
			type:0,
			isRender:false,
			mConfigs:{},
			outParams:{},
			extensionObj:{}
		}
	},
	computed:{
		PageConf:function(){
                    return JSON.parse(this.mConfigs,util.dealFunction).PageConf;
                },
	},
	methods:{ 
			_method1(node,_coms){
					let _this=this,option=_coms.option;
					let eventConf=option.eventConf;
					if(eventConf!=undefined){
						if(eventConf.isOn){
							if(eventConf.method1!=undefined){
									eventConf.method1(node,_this,option);
							}
						}
					}
			},
			_method2(node,_coms){
						let _this=this,option=_coms.option;
						let eventConf=option.eventConf;
						if(eventConf!=undefined){
						if(eventConf.isOn){
							if(eventConf.method2!=undefined){
								eventConf.method2(node,_this,option);
							}
						}
					}
			},
			_method3(node,_coms){
					let _this=this,option=_coms.option;
					let eventConf=option.eventConf;
					if(eventConf!=undefined){
					if(eventConf.isOn){
						if(eventConf.method3!=undefined){
								eventConf.method3(node,_this,option);
						}
					}
					}
			},
			_method4(node,_coms){
					let _this=this,option=_coms.option;
					let eventConf=option.eventConf;
					if(eventConf!=undefined){
					if(eventConf.isOn){
						if(eventConf.method4!=undefined){
							eventConf.method4(node,_this,option);
						}
					}
					}
			},
			_initEvent(){
				let _this=this,option=this.PageConf;
				if(option.InterceptEvent!=undefined){
					let eventConf=option.InterceptEvent.init;
					if(eventConf!=undefined){
							if(eventConf.isOn){
								eventConf.event(_this,option);
							}
						}
				}
           },
			init(){
				this.isRender=false;
				const toast = this.$createToast({
                time: 0,
                txt: '配置数据读取中..'
                })
              	toast.show()
				let moduleCode=this.$route.params.tableName;
				let inputArr=[
					{dataField:"ExtensionTwo", op:'EQ', dataValue:moduleCode},
					{dataField:"VersionNum", op:'EQ', dataValue:webappConfig().sysConf.version}
					],params={};
                params.queryConditionItem=inputArr;
                fetch({
                     url: '/api/services/app/reportConf/getMainObjectForEdit',
                     method: 'post',
                     data: params
                }).then((data)=>{
                      if(data.success){
						  if(data.result.order!=null){
								this.mConfigs=data.result.order.baseInfoConf;
								this.isRender=true;
								
							}else{
								this.$router.push({ path:'/Page404'});
							}
							toast.hide()
                      } 
                      else {
						  this.$message.error('失败！'+data.error.message);
						  toast.hide()
                      }
                });
        },
	},
	beforeMount(){
	},
	mounted(){
		this.init();
    },
	components:{
		renderContainerLay
	},
	watch:{
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus" scoped>
</style>



